у заводу и захватим его. Теперь мы способны производить японскую боевую технику! Правда, это всего лишь \"Тенгу\" — занятная машинка, способная переключаться в два режима: противопехотная парящая машина и быстрый истребитель. Но так или иначе, Тенгу летает, а значит его можно перебрасывать его куда потребуется. С суши морская база не защищена, и Тенгу легко ее разнесут. Затем наши бравые ребята напоят молотовским коктейлем гарнизоны зданий в центре карты и захватят госпиталь, после чего дело пойдет весело и с треском. Главная база защищена, но не от парашютистов, которые могут высадиться в точке; а если одновременно Николай пойдет брать базу в лоб, продержится она считанные секунды. Собственно, так и произошло. ")]),_c('div',{staticClass:"narration"},[_vm._v(" МИССИЯ 3: \"МАРШ КРАСНОЙ АРМИИ\" Следующая операция — против Союзников. Наше руководство решило, что сердце Альянса находится в непосредственной близости от его кошелька, и спланировало атаку на Женеву — средоточие капитала. В городе есть энное количество швейцарских банков; их рекомендуется захватить, чтобы получить дополнительный приток капитала. База наша располагается… непосредственно на Женевском озере, что не дает возможности строить танки и пехоту. Сравнительно дешево при помощи Скатов и близкого расположения катушек Теслы можно захватить причал на севере, но оттуда на фронт войска придется перебрасывать с помощью тех же Голиафов. Поэтому там стоит сделать разве что казармы, чтобы захватить тамошние два банка, и то не факт. Но первая наша проблема — отразить морские атаки. К счастью, у нас скоро будут подлодки. Турелей здесь нужно немало, как зенитных, так и — в первую очередь — катушек Теслы. Но когда с верфей Союзников поползут авианосцы, надо встретить их на подходе и торпедировать подлодками. Авианосец, как и советский дредноут, может здорово попортить шкуру базе. Если атаки отражены, нам предложат строиться на южном берегу; и вот там можно уже развернуться. Теслоносцы и танки легко подавят (местами